@import url(https://fonts.googleapis.com/css?family=Fira+Sans|Oxygen:300,400&display=swap);
:root {
  --headfont: 'Fira Sans', sans-serif;
  --bodyfont: 'Oxygen', sans-serif;
  --white: #ffffff;
  --offwhite: #f9f9fc;
  --darkgray: #1b1d1e;
  --gray: #4D4D4D;
  --lightgray: #999999;
  --green: #C2E15F;
  --orange: #FDA333;
  --purple: #D3A4F9;
  --red: #fb5344;
  --pinkred: #FB4485;
  --blue: #6CE0F1;
  --salmon: #fa8a90;
  --salmon-light: #fa9ba1;
  --cyan : #8afaf4;
  --shadow: #cccccc;

  color: #4D4D4D;

  color: var(--gray);
  background-color: #f9f9fc;
  background-color: var(--offwhite);

  font-family: 'Oxygen', sans-serif;

  font-family: var(--bodyfont);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  padding: 0;
  margin: 0;
  outline: none;
}

html, body {
  width: 100%;
  min-height: 100%;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Fira Sans', sans-serif;
  font-family: var(--headfont);
  margin-bottom: 0.5rem;
}

h1 { font-size: 3.0rem; }
h2 { font-size: 2.5rem; }
h3 { font-size: 2.0rem; }
h4 { font-size: 1.5rem; }
h5 { font-size: 1.0rem; }
h6 { font-size: 0.5rem; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  padding: 3rem 1.5rem;
}

ul, ol {
  margin-left: 1rem;
}

.side-bar {
  padding: 2rem 3rem 2rem 1rem;
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: flex-start;
  background-color: var(--darkgray);
  color: var(--white);
}

.side-header {
  display: flex;
  flex-direction: column;
  align-content: stretch;
  justify-content: flex-start;
}

.side-header > * {
  margin-bottom: 0.5rem;
}

.profile {
  width: 50%;
  padding-top: 50%;
  margin: 0 auto;
  background: url(/static/media/profile.438e1c2a.jpg) no-repeat center center;
  background-size: cover;
  border-radius: 100%;
}

.name {
  text-align: center;
  font-size: 2rem;
  font-family: var(--normalfont);
  font-weight: 300;
}

.job {
  text-align: center;
  font-size: 1rem;
  font-family: var(--normalfont);
  font-weight: 300;
}

.side-socials {
  margin-top: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  font-size: 1.2rem;
  color: var(--offwhite);
}

.icon-link {
  transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out, -webkit-transform 0.25s ease-in-out;
  padding: 15px;
  color: inherit;
}

.icon-link:hover {
  -webkit-transform: scale(1.25);
          transform: scale(1.25); 
}

.Carosell {
  justify-content: stretch;
  align-content: stretch;
  position: relative;
}

.Carosell-slides {
  position: absolute;
  bottom: 1rem;
  right: 2rem;
  z-index: 5;
  color: var(--white);
  font-size: 1.25rem;
  cursor: pointer;
}

.Carosell-slides > * {
  margin-left: 0.5rem;
}

.Slide {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 2rem;
  background: var(--darkgray) no-repeat center/cover fixed;
}

.active-slide-enter {
  opacity: 0;
}

.active-slide-enter-active {
  opacity: 1;
  transition: all 1.5s ease;
}

.active-slide-exit {
  z-index: -1;
  opacity: 1;
}

.active-slide-exit-active {
  opacity: 0;
  transition: all 1.5s ease 1.5s;
}

.Slide .Slide-text:first-child {
  font-size: 3rem;
}

.Slide-text {
  color: var(--white);
  text-align: center;
  font-size: 1.5rem;
}

.timeline-wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2rem 2rem;
}

.timeline-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  right: 50%;
  border: 1px solid var(--darkgray);
}

.timeline-item-wrapper {
  -webkit-filter: drop-shadow(0px 10px 10px var(--shadow));
          filter: drop-shadow(0px 10px 10px var(--shadow));
  --arrow-width: 2rem;
  --arrow-top: 20%;
  width: 45%;
}

.timeline-item {
  background-color: var(--white);
  padding: 2rem 1rem;
  border-radius: 10px;
  position: relative;
}

.timeline-item-date {
  position: absolute;
  bottom: 0.625rem;
  right: 1.5rem;
  color: var(--lightgray);
  font-size: 0.75rem;
  font-family: var(--headfont);
}

.timeline-item:before {
  width: calc(var(--arrow-width) / 2);
  height: var(--arrow-width);
  background-color: var(--white);
  display: block;
  position: absolute;
  content: '';
  top: var(--arrow-top);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(2n+1) {
  margin-top: 6rem;
}

.timeline-wrapper .timeline-item-wrapper:nth-child(2n) .timeline-item:before {
  left: 100%;
  -webkit-clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
          clip-path: polygon(0% 0%, 100% 50%, 0% 100%);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(2n+1) .timeline-item:before {
  right: 100%;
  -webkit-clip-path: polygon(0% 50%, 100% 0%, 100% 100%);
          clip-path: polygon(0% 50%, 100% 0%, 100% 100%);
}

.timeline-icon {
  position: absolute;
  top: calc(var(--arrow-top) + var(--arrow-width) / 2 - 2rem);
  background-color: var(--blue);
  font-size: 2rem;
  padding: 1.25rem 0.25rem;
  border-radius: 100%;
  color: var(--white);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(2n) .timeline-icon {
  left: calc(111% - 2.125rem);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(2n+1) .timeline-icon {
  left: calc(-11% - 2.125rem);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+1)>.timeline-icon {
  background-color: var(--green);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+2)>.timeline-icon {
  background-color: var(--orange);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+3)>.timeline-icon {
  background-color: var(--purple);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+4)>.timeline-icon {
  background-color: var(--red);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+5)>.timeline-icon {
  background-color: var(--pinkred);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+6)>.timeline-icon {
  background-color: var(--blue);
}

.timeline-wrapper .timeline-item-wrapper:nth-child(7n+7)>.timeline-icon {
  background-color: var(--salmon);
}

@media only screen and (min-width: 750px) and (max-width: 1300px) {
  .timeline-item-wrapper {
    width: 42.5% !important;
  }

  .timeline-wrapper .timeline-item-wrapper:nth-child(2n) .timeline-icon {
    left: calc(117.6470688% - 2.125rem);
  }

  .timeline-wrapper .timeline-item-wrapper:nth-child(2n+1) .timeline-icon {
    left: calc(-17.6470688% - 2.125rem);
  }
}

@media only screen and (max-width: 750px) {
  .timeline-line {
    left: 10%;
    right: 90%;
  }

  .timeline-wrapper {
    flex-direction: column;
  }

  .timeline-item-wrapper {
    width: 80%;
    margin-left: 20%;
    margin-top: 1rem !important;
  }

  .timeline-item:before {
    left: unset !important;
    right: 100% !important;
    -webkit-clip-path: polygon(0% 50%, 100% 0%, 100% 100%) !important;
            clip-path: polygon(0% 50%, 100% 0%, 100% 100%) !important;
  }

  .timeline-icon {
    right: calc(112.5% - 1rem) !important;
    left: unset !important;
  }
}

#Main {
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 1fr;
  transition: all 0.25s ease-in-out;
}

#menu-btn {
  position: fixed;
  top: 0;
  left: calc(300px - 3.5rem);
  color: var(--white);
  font-size: 1.5rem;
  padding: 1rem;
  cursor: pointer;
  z-index: 9999;
  transition: left 0.25s ease-in-out 0.25s, -webkit-transform 0.25s ease-in-out 0s;
  transition: transform 0.25s ease-in-out 0s, left 0.25s ease-in-out 0.25s;
  transition: transform 0.25s ease-in-out 0s, left 0.25s ease-in-out 0.25s, -webkit-transform 0.25s ease-in-out 0s;
}

.hide-nav #menu-btn {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  padding-left: calc(100vw - 3rem);
}

.hide-nav {
  width: calc(100vw + 300px - 3.5rem) !important;
  -webkit-transform: translateX(calc(-300px + 3.5rem));
          transform: translateX(calc(-300px + 3.5rem));
}

#Hero {
  height: 100vh;
}

#Main-Content {
  overflow: hidden scroll;
}

